import { compR } from "../func/compr";
import { iterator1 } from "../iterator";
export function filter(pred, src) {
    return src
        ? iterator1(filter(pred), src)
        : (rfn) => {
            const r = rfn[2];
            return compR(rfn, (acc, x) => (pred(x) ? r(acc, x) : acc));
        };
}
