import { implementsFunction, isMap, isSet } from "@thi.ng/checks";
export const empty = (x, ctor) => implementsFunction(x, "empty")
    ? x.empty()
    : new (x[Symbol.species] || ctor)();
export const copy = (x, ctor) => implementsFunction(x, "copy")
    ? x.copy()
    : new (x[Symbol.species] || ctor)(x);
export const first = (x) => x[Symbol.iterator]().next().value;
export const objValues = (src) => {
    const vals = [];
    for (let k in src) {
        src.hasOwnProperty(k) && vals.push(src[k]);
    }
    return vals;
};
export const ensureMap = (x) => isMap(x) ? x : new Map(x);
export const ensureSet = (x) => isSet(x) ? x : new Set(x);
